* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .bg {
    padding: 40px;
    background: #eff3f6;
  }
  
  .profile {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 400px;
    margin: 0 auto;
    border-radius: 3px;
    background-color: #fff;
    border: 1px solid #e6e6e6;
  
    margin-top: 50px;
    margin-bottom: 50px;
  }
  
  .description {
    text-align: center;
  }
  
  .avatar {
    width: 130px;
    border-radius: 50%;
    margin-top: 40px;
    margin-bottom: 20px;
  }
  
  .name {
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .tag,
  .location {
    margin-bottom: 5px;
    color: #768696;
  }
  
  .label {
    color: #768696;
  }
  
  .stats {
    width: 100%;
    height: 100px;
    list-style: none;
    display: flex;
    justify-content: space-around;
    background-color: #f3f6f9;
    text-align: center;
  }
  
  .stats > li {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100px;
    border-top: 1px solid #e6e6e6;
  }
  
  .stats > li:not(:last-child) {
    border-right: 1px solid #e6e6e6;
  }
  
  .quantity {
    font-weight: bold;
  }